import createPqcModule from "../public/pqc.js";

let Module = null;

export async function initPQC() {
  if (!Module) {
    Module = await createPqcModule();
  }
  return Module;
}

// ML-KEM-768 Sizes
const PK_SIZE = 1184;
const SK_SIZE = 2400;
const CT_SIZE = 1088;
const SS_SIZE = 32;

export async function kemEncapsulate(publicKeyBase64) {
  const module = await initPQC();

  const publicKey = Uint8Array.from(atob(publicKeyBase64), (c) =>
    c.charCodeAt(0)
  );

  const kemPtr = module._malloc(CT_SIZE);
  const sharedPtr = module._malloc(SS_SIZE);

  const pubPtr = module._malloc(PK_SIZE);
  module.HEAPU8.set(publicKey, pubPtr);

  module._pqc_encaps(kemPtr, sharedPtr, pubPtr);

  const kemCipher = module.HEAPU8.slice(kemPtr, kemPtr + CT_SIZE);
  const sharedSecret = module.HEAPU8.slice(sharedPtr, sharedPtr + SS_SIZE);

  module._free(kemPtr);
  module._free(sharedPtr);
  module._free(pubPtr);

  return {
    kemCiphertext: kemCipher, // Uint8Array
    sharedSecret  // Uint8Array
  };
}

export async function kemDecapsulate(kemCipherBase64, privateKeyBase64) {
  const module = await initPQC();

  const kem = Uint8Array.from(atob(kemCipherBase64), (c) =>
    c.charCodeAt(0)
  );
  const secretKey = Uint8Array.from(atob(privateKeyBase64), (c) =>
    c.charCodeAt(0)
  );

  const sharedPtr = module._malloc(SS_SIZE);

  const kemPtr = module._malloc(CT_SIZE);
  module.HEAPU8.set(kem, kemPtr);

  const skPtr = module._malloc(SK_SIZE);
  module.HEAPU8.set(secretKey, skPtr);

  module._pqc_decaps(sharedPtr, kemPtr, skPtr);

  const sharedSecret = module.HEAPU8.slice(sharedPtr, sharedPtr + SS_SIZE);

  module._free(sharedPtr);
  module._free(kemPtr);
  module._free(skPtr);

  return sharedSecret;
}

