// pages/chat.js
import { useEffect, useState, useRef } from "react";
import io from "socket.io-client";
import { useRouter } from "next/router";

import {
  kemEncapsulate,
  kemDecapsulate
} from "../utils/pqc";

import {
  deriveAESKey,
  aesEncrypt,
  aesDecrypt
} from "../utils/pqcMock"; // only AES from mock

let socket;

export default function Chat() {
  const router = useRouter();
  const [username, setUsername] = useState("");
  const [recipient, setRecipient] = useState("");

  const [text, setText] = useState("");
  const [messages, setMessages] = useState([]);
  const msgRef = useRef([]);

  const privateKeyRef = useRef("");

  // Load user session
  useEffect(() => {
    const user = sessionStorage.getItem("username");
    const sk = sessionStorage.getItem("pqc_private_key");

    if (!user) {
      router.push("/");
      return;
    }

    setUsername(user);
    privateKeyRef.current = sk;

    socket = io();

    socket.on("connect", () => {
      socket.emit("register_user", user);
    });

    socket.on("receive_message", async (payload) => {
      const decrypted = await decryptMsg(payload);
      msgRef.current = [...msgRef.current, decrypted];
      setMessages(msgRef.current);
    });

    return () => {
      socket.disconnect();
    };
  }, []);

  async function decryptMsg(payload) {
    const sharedSecret = await kemDecapsulate(
      payload.kemCiphertext,
      privateKeyRef.current
    );

    const aesKey = await deriveAESKey(sharedSecret);

    const iv = Uint8Array.from(atob(payload.aesIv), (c) => c.charCodeAt(0));
    const cipher = Uint8Array.from(atob(payload.ciphertext), (c) =>
      c.charCodeAt(0)
    );

    const plain = await aesDecrypt(aesKey, iv, cipher);

    return {
      text: plain,
      sender: payload.sender,
      receiver: payload.receiver,
      timestamp: payload.timestamp
    };
  }

  async function loadHistory() {
    const res = await fetch(
      `/api/messages?user1=${username}&user2=${recipient}`
    );
    const data = await res.json();

    const out = [];
    for (let m of data) {
      out.push(await decryptMsg(m));
    }

    msgRef.current = out;
    setMessages(out);
  }

  async function sendMessage() {
    if (!recipient || !text) return;

    // Get receiver public key
    const res = await fetch(`/api/users/${recipient}`);
    const data = await res.json();
    const receiverPk = data.pqPublicKey;

    // PQC: Encapsulate
    const { kemCiphertext, sharedSecret } =
      await kemEncapsulate(receiverPk);

    // Derive AES key
    const aesKey = await deriveAESKey(sharedSecret);

    // Encrypt
    const { iv, ciphertext } = await aesEncrypt(aesKey, text);

    const payload = {
      sender: username,
      receiver: recipient,
      kemCiphertext: btoa(String.fromCharCode(...kemCiphertext)),
      aesIv: btoa(String.fromCharCode(...iv)),
      ciphertext: btoa(String.fromCharCode(...ciphertext)),
      timestamp: new Date().toISOString()
    };

    socket.emit("send_message", payload);

    msgRef.current = [
      ...msgRef.current,
      { sender: username, text, timestamp: payload.timestamp }
    ];
    setMessages(msgRef.current);
    setText("");
  }

  return (
    <div style={{ maxWidth: 700, margin: "auto", padding: 20 }}>
      <h2>Welcome {username}</h2>

      <input
        style={{ width: "60%" }}
        placeholder="Recipient username"
        value={recipient}
        onChange={(e) => setRecipient(e.target.value)}
      />

      <button onClick={loadHistory}>Load Chat</button>

      <div
        style={{
          marginTop: 20,
          height: 300,
          overflowY: "scroll",
          border: "1px solid black",
          padding: 10
        }}
      >
        {messages.map((msg, i) => (
          <div key={i} style={{ marginBottom: 10 }}>
            <b>{msg.sender}: </b> {msg.text}
          </div>
        ))}
      </div>

      <div style={{ marginTop: 15 }}>
        <input
          style={{ width: "70%" }}
          value={text}
          onChange={(e) => setText(e.target.value)}
        />
        <button onClick={sendMessage}>Send</button>
      </div>
    </div>
  );
}

